.item {
    display: flex;
    justify-content: left;
    text-align: right;
    align-items: center;
    /* box-shadow: 10px 11px 16px 4px rgba(0, 0, 0, 0.7); */
    box-shadow: 12px 14px 8px 2px rgba(0, 0, 0, 0.75);
    margin-bottom: 50px;
    padding: 5px 15px 5px 5px;
    border: 0px;
    border-radius: 10px 10px 10px 10px;
    background-color: #282c34;
}

.item-p {
    color: white;
    font-weight: 700;
    cursor: pointer;
    text-align: left;
    width: 79%;
}

.item-p-complete {
    text-decoration: line-through;
}

.delete {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 25px;
    font-weight: 400;
    width: 25px;
    border-radius: 25px;
    height: 25px;
    padding: 10px;
    margin-right: -10px;
    cursor: pointer;
    background-color: white;
    text-align: center;
    align-items: center;
    position: absolute;
    right: 10%;
}

.delete:hover {
    background-color: rgb(218, 218, 218);
    color: red;
}

.icon {
    float: right;
    font-size: 30px;
    font-weight: 400;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
}

.icon-check {
    color: white;
    margin-left: -1px;
    margin-right: 5px;
}

.icon-check-active {
    color: chartreuse;
}

.icon-check-false {
    color: white;
}